/*라우터 설정*/

import { createWebHistory, createRouter } from "vue-router";
import r_List from './components/List.vue';
import r_home from './components/Home.vue';
import r_detail from './components/Detail.vue';
import r_author from './components/Author.vue';
import r_comment from './components/Comment.vue';

const routes = [
  {
    path: "/list",
    component: r_List,
  },
  {
    path: "/",
    component: r_home,
  },
  {
    path: "/detail/:id",
    component: r_detail,
    children: [
        {
          path: "author",
          component: r_author,
        },
        {
          path: "comment",
          component: r_comment,
        },
    ]
  }
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;